<template>
  <div class="item-out">
    <img v-bind:src="item.logo.url" v-on:click="open(item.id)">
    <h4 v-html="item.name.html" /> 
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  name: 'event-item',
  props: [
    'item'
  ],
  mounted () {
  },
  methods: {
    open (id) {
      this.$store.commit('setCurrentEvent', this.item);
      this.$router.push(`/event/${id}`)
    }
  }
}
</script>

<style>
.item-out {
  width: 100%;
  text-align: center;
  margin:16px 0px;
  color: #fff;
  background-color: rgba(0,0,0,.85);
}

.event-overlay {
  position: absolute;
  z-index: 333;
  background-color: rgba(0,0,0,.85);
  width: 100%;
  text-align: left;
  padding: 12px;
  color: #fff;
}

.item-out img {
  width: 100%;
}

.item-out h4 { 
  color: #fff;
  margin-top:8px;
  padding-bottom:8px;
}
</style>


