<template>
  <div id="app">
    <header-view></header-view>
    <main>
      <div v-if="loaded">
      <router-view></router-view>
      </div>
      <div v-else>
        <b-spinner label="Loading..." style="width: 20rem; height: 20rem;" type="grow"></b-spinner>
      </div>
    </main>
    <footer-view></footer-view>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { api } from '@/utils/api'
import moment from 'moment'
import Header from '@/components/baseComponents/Header'
import Footer from '@/components/baseComponents/Footer'

export default {
  name: 'app',
  components: {
    'header-view': Header,
    'footer-view': Footer
  },
  data () {
    return {
      loaded: false,
      today : moment().format('MM/DD')
    }
  },
  mounted () {
    let hasData = false;
    
    if(this.eventData) {
      hasData = true
    } 

    if (this.lastUpdated.toString() === this.today.toString()) {
      if(hasData) {
        this.setData(this.eventData)
      }
      this.setLastUpdated(this.today)
      this.loaded = true
    } else {
       api.getRaw('/v1/eb/events/sanantonio').then(
      response => {
        this.events = response.data
        if (this.events.length > 0) {
          this.setData(this.events)
          this.setLastUpdated(this.today)
        }
        this.loaded = true;
      })
      .catch(error => {
        console.log(error)
      })
    }
    
  },
  computed: {
    ...mapGetters([
      'eventData',
      'lastUpdated'
    ])
  },
  methods: {
    ...mapActions([
      'setData',
      'setLastUpdated'
    ])
  }
}
</script>

<style>
body {
  margin: 0;
  background: url('./assets/core/background.png') fixed;
  background-repeat: no-repeat;
  background-color: #F8F8F8;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
}

main {
  text-align: center;
  margin-top: 124px;
  padding-bottom: 64px;
}

a {
  color: #fe9461;
}
</style>



