import { storage } from '@/utils/storage'
import { api } from '@/utils/api'
import moment from 'moment'

const state = {
  eventData: null,
  lastUpdated: null,
  currentEvent: null
}

const mutations = {
  setData (state, data) {
    state.eventData = data
  }, 
  setLastUpdated (state, data) {
    state.lastUpdated = data
  },
  setCurrentEvent(state, data) {
    state.currentEvent = data
  }
}

const actions = {
  
  getEventData ({ commit }) {
    let today = moment().format('MM/DD')
    commit('setData', storage.getValue('events'))
    return storage.getValue('events')
  },

  setData (context, data) {
    storage.storeValue('events', data)
    context.commit('setData', {state, data})
  },

  setLastUpdated (context, data) {
    storage.storeValue('lastUpdated', data)
    context.commit('setLastUpdated', data)
  },

  getEventById (context, id) {
    let events = storage.getValue('events');
    for(let i=0; i<events.length; i++){
      if(events[i].id === id) {
        context.commit('setCurrentEvent', events[i])
      }
    }
  }

}

const getters = {
  lastUpdated (state) {
    let lu = storage.getValue('lastUpdated');
    if(lu.toString().length > 0){
      return lu
    } else {
      return state.lastUpdated
    }
  },

  eventData (state) {
   return state.eventData
  },

  currentEvent (state) {
    return state.currentEvent
  }

}

export default {
  state,
  actions,
  mutations,
  getters
}



