<template>
  <div v-if="currentEvent">
    <div style="margin-bottom:48px;" class="event-final">
      <p v-on:click="goBack">back</p>
      <div style="margin:2px;">
        <div style="width:100%; margin-top:48px; text-align:center;">
          <img v-bind:src=currentEvent.logo.url style="width:100%; max-width:480px;"/>
        </div>
        <h3 v-html=currentEvent.name.html></h3>
        <br/>
        <span v-html=currentEvent.description.html></span>
        <button @click="openlink(`${currentEvent.url}`)">MORE INFORMATION</button>
      </div>
      <p v-on:click="goBack">back</p>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'event',
  data () {
    return {
      id: null,
      item: null
    }
  },
  mounted() {
    if(this.currentEvent === null) {
      this.getEventById(this.$route.params.id)
    }
  },
  methods: {
    openlink: function (target) {
      window.open(target, '_blank')
    },
    goBack() {
      this.$router.go(-1)
    },
    ...mapActions([
      'getEventById'
    ])
  },
  computed: {
    ...mapGetters([
      'currentEvent'
    ])
  }
}
</script>

<style>
.event-final {
  padding: 16px 8px;
  background-color: rgba(0,0,0,.87);
  width:100%;
}
</style>



