import { vm }  from '@/main'
import { storage } from '@/utils/storage'

const locationData = {
  city: 'San Antonio',
  phone: '844-230-6576',
  address: '101 Bowie St, 78205'
}

const socialNetworks = [
  { file: '/imgs/social/insta.png', name: 'Instagram', link: 'https://www.instagram.com/movelikewedo/' },
  { file: '/imgs/social/youtubes.png', name: 'Youtube', link: 'https://www.youtube.com/c/movelikewedo' },
  { file: '/imgs/social/facebook.png', name: 'Facebook', link: 'https://business.facebook.com/movelikewedo' },
  { file: '/imgs/social/twitter.png', name: 'Twitter', link: 'https://twitter.com/movelikewedo' }
]

const siteNavigation = [
]

const state = {
  currentTab: 0,
  socialData: socialNetworks,
  siteNavigation,
  locationData,
  currentLocation: null
}

const mutations = {
  setLocation (state, loc) {
    state.currentLocation = loc
  }
}

const getters = {
  socialNetworks (state) {
    return state.socialData
  },
  currentLocation (state) {
    return state.currentLocation
  },
  siteNavigation (state) {
    return state.siteNavigation
  },
  businessLocation (state) {
    return state.location
  },
  locationData (state) {
    return state.locationData
  }
}

const actions = {
  setAppLocation ({ commit, state }, obj) {
    storage.storeValue('location', obj)
    vm.$router.push(obj.path)
  }
}

export default {
  state,
  actions,
  mutations,
  getters
}



