<template>
  <div>
    <div v-for="item in navitems" v-bind:key="item._id">
      <menu-tile v-bind:item="item"></menu-tile>
    </div>
  </div>
</template>

<script>
import MainMenu from '@/components/uiComponents/MainMenu'

export default {
  name: 'main-menu',
  components: {
    'menu-tile': MainMenu
  },
  data () {
    return {
      navitems :[
        {img: 'menu_1', name: 'Hotel Amenities', index: 0, description: '<p>Information about the Marriott River Center</p>', path: '/amenities'},
        {img: 'menu_2', name: 'Tourism', index: 1, description: '<p>Sites and landmarks in the San Antonio area.</p>', path: '/tourism'},
        {img: 'menu_3', name: 'What\'s Happening', index: 2, description: '<p>Check out upcoming events and activities, brought to you by EventBrite.</p>', path: '/events'},
        {img: 'menu_4', name: 'Where to Eat', index: 2, description: '<p>Local favourites for eating out.</p>', path: '/dining'}
      ]
    }
  }
}
</script>

<style>

</style>



