<template>
  <div class="events-main">
    
      <div v-for="event in eventData" v-bind:key="event._id">
        <event-item v-bind:item="event"></event-item>
      </div>
    
  </div>
</template>

<script>
import { api } from '../utils/api'
import  { storage } from '@/utils/storage'
import EventItem from '@/components/uiComponents/EventItem'
import { mapGetters, mapActions } from 'vuex'
import moment from 'moment'

export default {
  name: 'events',
  components: {
    'event-item': EventItem
  },
  data () {
    return {
      today : moment().format('MM/DD')
    }
  },
  mounted () {
    
    this.$store.dispatch('getEventData').then((response) => {
      
    });
    
  },
  methods: {
    ...mapActions([
      'getEventData'
    ])
  },
  computed: {
    ...mapGetters([
      'eventData'
    ])
  }

}
</script>

<style>
.events-main {
  color: #fff;
}
</style>



