<template>
  <div>
    <header>
      <div class="logo-main"> 
        <img src="../../assets/logo.png" 
          alt="Vue.js PWA" 
          v-on:click="setAppLocation({path: '/', index: 0})"
          />
      </div>
      <div class="city-label">
         <h3>{{locationData.city}}</h3>
         <p><a v-bind:href="getNumber">{{locationData.phone}}</a><br/>{{locationData.address}}</p>
      </div>
    </header>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'header-view',
  methods: {
    ...mapActions([
      'setAppLocation'
    ])
  },
  computed: {
    getNumber () {
      let num = this.locationData.phone
      num = num.replace(/-/g,'')
      return `tel:${num}`
    },
    ...mapGetters([
      'locationData'
    ])
  }
}
</script>

<style>
header {
  top: 0;
  width: 100%;
  position: fixed;
  margin: 0;
  border-top:solid 6px #000;
  border-bottom:solid 2px #000;
  background-color: #efefef;
  height: 84px;
  z-index: 10000;
}

.logo-main {
  width: 118px;
  height: 100px;
  background: #000;
  z-index: 11000;
  margin-left: 8px;
  border-left: solid 2px #fff;
  border-right: solid 2px #fff;
  border-bottom: solid 2px #fff;
  float: left;
}

.city-label {
  width: 240px; 
  float: left;
  text-align: left;
  padding-left: 8px;
  margin-top: 0px;
  color: #fe9461;
}

.city-label h3 {
  padding: 0px; 
  margin: 0px;
  margin-bottom: 0px;
}

.city-label p {
  font-size: small;
  padding: 0px; 
  margin: 0px;
  color: #000;
}


.logo-main img { width: 100%; padding-left: 8%; padding-right: 8%; padding-top:36px; }
</style>



