<template>
  <div class="menu-item-main" v-on:click="setAppLocation({path: item.path, index: item.index})">
    <div class="menu-overlay">
      <h3>{{item.name}}</h3>
      <span v-html="item.description"></span>
    </div>
    <img :src="myimg" 
      style="width: 100%;"/>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  name: 'main-menu-item',
  props: [
    'item'
  ],
  computed: {
    myimg: function () {
      return require(`../../assets/menus/${this.item.img}.png`);
    }
  },
  methods: {
    ...mapActions([
      'setAppLocation'
    ])
  }
}
</script>

<style>
.menu-item-main {
  cursor: pointer;
  margin:16px 0px;
  width: 100%;
  height: 100%;
  border-top: solid 1px #333;
  border-bottom: solid 1px #333;
}

.menu-overlay {
  position: absolute;
  z-index: 333;
  background-color: rgba(0,0,0,.85);
  width: 100%;
  text-align: left;
  padding: 12px;
  margin:16px 0px;
}

h3 { 
  margin-bottom: 4px;
  color: #fe9461;
}

p {
  color: #fff;
}
</style>



